import { getDevelopersByCountry } from "@/services/developers/getDevelopersByCountry";
import { getDevelopersByState } from "@/services/developers/getDevelopersByState";
import { NextApiRequest } from "next";

export async function GET(request: NextApiRequest, params: Promise<{params: {country: string, state: string }}>) {
	const p = await params;
	const country = p.params.country;
	const state = p.params.state;

	console.log({country, state});

	if (country) {
		return Response.json(getDevelopersByCountry(country));
	}

	if (state) {
		return Response.json(getDevelopersByState(state));
	}

}
